@import '../_variables';

.character-list {
	list-style: none;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	width: 100%;

	&__item {
		margin: 0 1.5625rem 1.875rem;
		width: 12.5rem;
	}

	&__button {
		margin: auto;
		width: 10.625rem;
	}
}